import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

// 导入组件
import index from '../components/index.vue'//首页
import git_use from '../components/detail/git_use.vue'//Git使用基本流程
import git_morekf from '../components/detail/git_morekf.vue'//Git多人协同开发产生冲突问题
import vue_host from '../components/detail/vue_host.vue'//Vue-处理线上及本地的请求链接
import vue_font from '../components/detail/vue_font.vue'//vue项目打包后字体文件路径出错的解决办法
import vue_qingqiu from '../components/detail/vue_qingqiu.vue'//Vue跨域请求

export default new Router({
  mode: 'history',
  routes: [
    {path: '/',redirect:'index',component: index},
    {path: '/index.html',name: 'index',component: index},
    {path: '/git_use',name: 'git_use',component: git_use},
    {path: '/git_morekf',name: 'git_morekf',component: git_morekf},
    {path: '/vue_host',name: 'vue_host',component: vue_host},
    {path: '/vue_font',name: 'vue_font',component: vue_font},
    {path: '/vue_qingqiu',name: 'vue_qingqiu',component: vue_qingqiu},
    
  ]
})



